
.contact-content-box{
	padding: .3vw 0vw 3vw 3.2vw;
/*	background-image: url('../../src/img/contact_bg3.png');*/
	background-image: url('../img/contact_bg5.webp');
	background-size: 36vw 30vw;
	background-repeat: no-repeat;
	background-position: right center;
}
.contact-content-box h1{
	font-size: 8vw;
	font-family: 'Poppins',sans-serif;
	font-weight: bold;
}
.contact-content-box ul{
	padding: 0vw;
}

/*contact-items----------------------------------------------*/
.contact-items-box{
	margin: 2vw 0vw;
	overflow: hidden;
}
.contact-items{
	font-size: 3vw;
	color: #5e17eb;
	overflow-wrap: break-word;
}
.contact-items span{
	color: #5e17eb;
	display: inline-block;
	margin-right: 1vw;
}

/*nav social-------------------------------------------------*/
.nav-social-text{
	font-family: 'Poppins',sans-serif;
	font-size: 1.3vw;

}
.contact-content-box .nav-social-icon{
	color: #5e17eb;
	font-size: 3vw;
	margin: 0vw 1vw 0vw 0vw;
}
.contact-content-box .nav-social-icon:hover{
	color: black;
}



/*media query*********************************************************************/
/*extra small*/
@media screen and (max-width:576px){
/*contat outer box--------------------------------------------------*/
	.contact-content-box{
		background-size: 80vw 92vw;
		background-position: bottom right;
		padding: .3vw 0vw 3vw 2.2vw;
	}
	.contact-content-box h1{
	font-size: 23vw;
	}	

	/*contact-items----------------------------------------------*/
	.contact-items-box{
		margin: 5vw 0vw 7vw 0vw;
	}
	.contact-items{
		font-size: 8vw;
	}
	.contact-items span{
		margin-right: 1vw;
	}

	/*nav social-------------------------------------------------*/
	.nav-social-text{
		font-size: 6vw;
		margin: 1.7vw 0vw;

	}
	.contact-content-box .nav-social-icon{
		font-size: 11vw;
		margin: 0vw 2vw 0vw 0vw;
	}


}


